@import "../../global.scss";


.intro{
    background-color: white;
    display: flex;

    .left {
        flex: 0.5;
        overflow: hidden;
        align-items: center;

        .imgContainer{
            border-radius: 50%;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            float: right;

            img{
                width: 100%;
                height: 100%;
            }
        }
    }

    .right {
        flex: 0.5;
        position: relative;
        .wrapper{
            width:100%;
            height: 100%;
            padding-left: 50px;
            display:flex;
            flex-direction: column;
            justify-content: center;

            h1{
                font-size: 60px;
                margin: 10px 0;
            }

            h2{
                font-size: 35px;
            }

            h3{
                font-size: 30px;

                span {
                    font-size: inherit;
                    color: $mainColor;
                }

                .ityped-cursor{
                    animation: blink 1s infinite;

                }

                @keyframes blink {
                    50%{
                        opacity:1;
                    }
                    100%{ 
                       opacity:0;
                    }
                }
            }
        
            a{
                position: absolute;
                bottom: 10px;
                left: 75%;

                .icon{
                    font-size: 80px;
                    font-weight: 700;
                    text-decoration: none;
                    color:$mainColor;
                    animation: arrowBlink 3s infinite ;
                }

                @keyframes arrowBlink {
                    to{
                        opacity: 0;
                    }
                    
                }
            }
        }
    }
}